.container {
  display: flex;
  flex-direction: row;
}

.container .left {
  flex: 40%;
  width: 40%;
  display: flex;
  flex-direction: column;
  row-gap: 0.5vw;
  padding-right: 0.5vw;
}

.container .left div {
  display: flex;
  flex-direction: row;
}

.container .left .label {
  flex: 20%;
  text-transform: capitalize;
}

.container .left .input {
  flex: 80%;

}

.container .left .input span {
  margin-left: 0.5vw;
  font-weight: bold;
}


.container .left .label2 {
  flex: 20%;
  text-transform: capitalize;
}

.container .left .input2 {
  flex: 20%;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  justify-content: center;
}

.container .left .input3 {
  flex: 60%;
  margin-left: 0.5vw;
  padding: 10px;
  font-weight: bold;
  font-size: 1.5em;
}

.container .right {
  flex: 60%;
  width: 60%;
  padding-left: 0.5vw;
}

.container .right .table {
  max-height: 75vh;
  overflow: auto;
  /* background-color: aqua; */
}

.container .right .table::-webkit-scrollbar {
  display: none;
}

.buttonFixAction {
  position: sticky;
  right: 0;
  width: 7dvw;
  max-width: 7dvw;
  z-index: 0;
  cursor: pointer;
  background-color: var(--border-color);
}

.buttonFixAction:hover {
  background-color: var(--header-color-light);
  cursor: pointer;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1vw 0;
}

.buttons2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  padding: 1vw 0;
}

.buttons2 .left {
  flex: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1vw 0;
}

.buttons2 .right {
  flex: 10%;
  display: flex;
  flex-direction: row;
  justify-content: end;
  /* gap: 9px; */
  padding: 1vw 0;
  /* border: 1px solid red; */
}

.buttons3 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 1vw 0;
}

.buttons .command {
  display: flex;
  flex-direction: row;
  column-gap: 0.5vw;
}

.btrefresh {
  background-color: white;
  border-radius: 10px;
  margin-left: 1.5vw;
  margin-top: auto;
  padding: 1vw;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 12px;
  font-weight: 600;
}


.inputedit {
  margin-bottom: 3px;

  @media (min-width: 1081px) {
    width: 100%;
  }

  @media (max-width: 1080px) {
    width: 100%;
  }
}


.inputadd {
  @media (min-width: 1081px) {
    width: 90%;
  }

  @media (max-width: 1080px) {
    width: 90%;
  }
}

.containerleftright {
  /* background-color: blanchedalmond; */

  display: flex;
  gap: 2vw;

  @media (min-width: 1081px) {
    flex-direction: row;
  }

  @media (max-width: 1080px) {
    flex-direction: column;
  }
}

.containerleftright .leftposition {
  flex: 50%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.containerleftright .rightposition {
  flex: 50%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.containerForm {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0.25rem;
}

.containerFormItem {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
}

.containerfilterdate {
  display: flex;
  flex-wrap: wrap;

  @media (min-width: 937px) {
    flex-direction: row;
  }

  @media (max-width: 936px) {
    flex-direction: column;
  }
}

.containerfilterdate .filterdate {
  flex-basis: 60%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  column-gap: 1vw;
  padding: 1.2vw 0;

  @media (min-width: 937px) {
    order: 1;
  }

  @media (max-width: 936px) {
    order: 2;
  }
}

.containerfilterdate .buttons {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  column-gap: 1vw;
  padding: 0.5vw;

  @media (min-width: 440px) {
    order: 2;
  }

  @media (max-width: 439px) {
    order: 1;
  }
}

.containerfiltertext {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 1vw;
  margin-bottom: 1vh;
  padding-bottom: 1vh;
  /* border: 1px solid yellow; */
  /* max-height: 200px; */

  @media (min-width: 400px) and (max-width: 660px) {
    overflow-y: visible;
    overflow-x: auto;
  }

  @media (max-width: 400px) {
    overflow-y: visible;
    overflow-x: auto;
  }
}

.containerfiltertext input {

  @media (min-width: 400px) and (max-width: 660px) {
    width: 15vw;
  }

  @media (max-width: 400px) {
    width: 30vw;
  }
}

.labelforfiltertext {
  text-transform: capitalize;
  font-weight: 600;

  @media (min-width: 962px) {
    font-size: 0.8vw;
  }

  @media (min-width: 778px) and (max-width: 962px) {
    font-size: 1vw;
  }

  @media (min-width: 520px) and (max-width: 778px) {
    font-size: 1.2vw;
    text-align: center;
  }

  @media (min-width: 400px) and (max-width: 520px) {
    font-size: 1.5vw;
    text-align: center;
  }

  @media (max-width: 400px) {
    font-size: 2.5vw;
    text-align: center;
  }
}

.tablebarcodecontainer {
  height: auto;
  overflow: auto;
  margin-top: 1.5vh;
}

.bodyPopup {
  overflow-y: auto;
  max-height: 58vh;
}


/*============================= ONLY LAYOUTING ======================== */
.rowGrid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}

/*============================= ONLY LAYOUTING ======================== */